import React, {PropsWithChildren, useEffect, useState} from "react";
import {f7ready, Link, Navbar, NavLeft, NavTitle, Page, Toolbar} from "framework7-react";
import {LinkParams} from "../navigation/LinkParams";

export interface TabbarLayoutProps extends PropsWithChildren {
    screenTitle: string

    onClickBack(): void

    tabbarLinks(): LinkParams[]
}

export const TabbarLayout: React.FC<TabbarLayoutProps> = (props: TabbarLayoutProps) => {

    const [tabbarLinks, setTabbarLinks] = useState<LinkParams[]>([]);

    useEffect(() => {
        f7ready(() => {
            setTabbarLinks(props.tabbarLinks())
        })
    }, [])

    return (
        <Page>
            <Navbar>
                <NavLeft>
                    <Link iconIos="f7:chevron_left" onClick={props.onClickBack}>Back</Link>
                    <NavTitle>{props.screenTitle}</NavTitle>
                </NavLeft>
            </Navbar>
            {props.children}
            <Toolbar position="bottom" tabbar labels>
                {tabbarLinks.map((l: LinkParams) => {
                    return <Link tabLink className="text-align-center" key={l.name} {...l.props} text={l.name} />
                })}
            </Toolbar>
        </Page>
    )
}
