import React from "react";
import {TabbarLayout} from "../layouts/TabbarLayout";
import {Page} from "framework7-react";
import {LinkParams} from "../navigation/LinkParams";
import {QRScanError, QRScanInfo, QRScanner} from "../components/QRScanner";

export interface ScanProps {
    screenTitle: string

    tabbarLinks(): LinkParams[]

    onScan(scanInfo: QRScanInfo): void

    onError(scanError: QRScanError): void

    onClickBack(): void
}

export const Scan: React.FC<ScanProps> = (props: ScanProps) => {
    return (
        <Page>
            <TabbarLayout
                {...props}
            >
                <QRScanner onScan={props.onScan} onError={props.onError}/>
            </TabbarLayout>
        </Page>
    )
}
