import React from "react";
import {Page} from "framework7-react";
import {TabbarLayout} from "../layouts/TabbarLayout";
import {LinkParams} from "../navigation/LinkParams";
import {QRScanInfo} from "../components/QRScanner";

export interface ProductProps {
    scanInfo: QRScanInfo

    screenTitle: string

    tabbarLinks(): LinkParams[]

    onClickBack(): void
}

export const Product: React.FC<ProductProps> = (props: ProductProps) => {
    return (
        <Page>
            <TabbarLayout
                {...props}
            >
                {JSON.stringify(props.scanInfo)}
            </TabbarLayout>
        </Page>
    )
}
