import React, {useCallback, useEffect, useMemo} from "react";
import {Html5Qrcode} from "html5-qrcode";
import {Html5QrcodeCameraScanConfig, Html5QrcodeConfigs} from "html5-qrcode/esm/html5-qrcode";
import {Html5QrcodeError, Html5QrcodeResult, Html5QrcodeScanType} from "html5-qrcode/esm/core";
import {useWindowSize} from "../utils/useWindowSize";
import {f7ready} from "framework7-react";
import {Html5QrcodeScannerState} from "html5-qrcode/esm/state-manager";


export interface QRScanInfo {
    productId: string,
    productName: string
    manufacturerName: string
    quantityInLot: number
    productionDate: Date
    checksum: string
}

export interface QRScanError {

}

interface QRPayload {
    id: string
    pn: string
    mn: string
    bs: number
    pd: number
    cs: string
}

export interface QRScannerProps {
    fps?: number,

    onScan(scanInfo: QRScanInfo): void

    onError(error: string):void
}

interface Html5QrcodeScannerConfig extends Html5QrcodeCameraScanConfig, Html5QrcodeConfigs {
    rememberLastUsedCamera?: boolean | undefined;
    supportedScanTypes?: Array<Html5QrcodeScanType> | [];
    showTorchButtonIfSupported?: boolean | undefined;
    showZoomSliderIfSupported?: boolean | undefined;
    defaultZoomValueIfSupported?: number | undefined;
}

let readerElementCounter = 0;

export const QRScanner: React.FC<QRScannerProps> = (props: QRScannerProps) => {

    const wSize = useWindowSize();

    const scannerSize = useMemo(() => {
        return {
            width: wSize.width * 0.8,
            height: wSize.height * 0.8,
        }
    }, [wSize]);

    const elementId = useMemo(() => {
        return `qr-code-reader-${++readerElementCounter}`;
    }, []);

    const verify = useCallback((qrPayload: QRPayload) => {
        if (!qrPayload.id || !qrPayload.pn || !qrPayload.bs || !qrPayload.mn || !qrPayload.pd || !qrPayload.cs) {
            throw new Error(`QR code payload is malformed, id, pn, lc, mn, pd, sc is expected, ${qrPayload} given`);
        }
        // @TODO: checksum impl


    }, []);

    const success = useCallback((decodedText: string, result: Html5QrcodeResult) => {
        const qrPayload: QRPayload = JSON.parse(decodedText);

        try {
            verify(qrPayload)
        } catch(err) {
            props.onError(err?.toString() || "Error");
        }

        props.onScan({
            productId: qrPayload.id,
            productName: qrPayload.pn,
            productionDate: new Date(qrPayload.pd * 1000),
            manufacturerName: qrPayload.mn,
            quantityInLot: qrPayload.bs,
            checksum: qrPayload.cs
        });
    }, [props.onScan, verify]);

    const failed = useCallback((errorMessage: string, error?: Html5QrcodeError) => {

    }, [])

    useEffect(() => {
        const html5QrCode = new Html5Qrcode(elementId);
        let startPromise = main();

        async function main(): Promise<any> {
            const devices = await Html5Qrcode.getCameras();

            let currentCameraIndex = 0;
            if (devices && devices.length) {
                currentCameraIndex = devices.length - 1;
            } else {
                failed('Camera not found')
            }

            return html5QrCode.start(
                devices[currentCameraIndex].id,
                {
                    fps: 10,
                    qrbox: (viewfinderWidth, viewfinderHeight) => {
                        const minEdgeSize = Math.min(viewfinderWidth, viewfinderHeight);
                        const qrboxSize = Math.floor(minEdgeSize * 0.70);
                        return {
                            width: qrboxSize,
                            height: qrboxSize,
                        };
                    },
                },
                success,
                failed
            );
        }

        return () => {
            startPromise.finally(() => {
                html5QrCode.stop()
                    .catch((err) => {
                        console.warn("error while html5QrCode.stop()", err)
                    });
            });
        };
    }, []);

    return (
        <div id={elementId} style={{width: `${wSize.width}px`, height: `${wSize.width}px`}}>

        </div>
    )
}
